<template>
  <div class="orderDetail">
		<!--订单状态-->
		<div class="orderStatue">
			等待发货
		</div>
		<!--地址头部-->
		<section class="mod-address">
	    <a href="" class="flex_between mod-cell">
	    	<i class="iconfont icon-map"></i>
	        <div class="mod-bd">
	        	<h4 class="flex_between"> <span class="name fn-left">收货人: 廖立伟</span> <span class="tel fn-right">13251026170</span> </h4>
	        	<p class="address">收货地址：浙江省杭州市西湖区三墩镇振华路86号西港新界东区x憧8楼110室</p>
	        </div>
	      <i class="iconfont icon-more"></i>
	    </a>
	  </section>
    <!--订单详情部分-->
    <div class="order_detailInfo">
    	<div class="title-box flex_star">
    		<p>众筹标题</p>
    	</div>
    	<div class="order-info flex_star">
        <div class="img-box"> 
        	<img src="../../assets/images/img-repay.jpg" alt="" /> 
        </div>
        <div class="info">
          <p class="title">【首发礼】-超薄音响1台</p>
          <p class="current flex_between"> <span class="price">￥100</span> <span class="count">x1</span> </p>
        </div>
      </div>
	    <!--服务承若-->
	    <div class="commitment-box flex_between"> 
	    	<div class="flex_star">
	    		<i class="iconfont icon-7tianbaotui"></i>
	    		<i class="iconfont icon-tuihuobaozhang"></i>
	    	</div>
	        <p>承诺服务: 7天无理由，一年质保</p>
	    </div>
	    <!--众筹价格-->
	    <div class="fee-box">
	    	<div class="fee-item flex_between">
	    		<p>众筹价格</p>
	    		<p>￥100</p>
	    	</div>
	    	<div class="fee-item flex_between">
	    		<p>配送费</p>
	    		<p>￥0</p>
	    	</div>
	    </div>
	    <!--总价-->
	    <div class="total-fee flex_between">
	    	<p class="key-p">合计</p>
	    	<p class="value-p">￥100</p>
	    </div>
    </div>
    <!--订单编号-->
    <div class="order_number">
    	<div class="order_number_item flex_star">
    		<p>订单编号：</p>
    		<p>12121131313654654654</p>
    	</div>
    	<div class="order_number_item flex_star">
    		<p>创建时间：</p>
    		<p>2018-04-25 15:28:10</p>
    	</div>
    	<div class="order_number_item flex_star">
    		<p>付款时间：</p>
    		<p>2018-04-25 15:28:10</p>
    	</div>
    	<div class="order_number_item flex_star">
    		<p>发货时间：</p>
    		<p>2018-04-25 15:28:10</p>
    	</div>
    	<div class="order_number_item flex_star">
    		<p>收货时间：</p>
    		<p>2018-04-25 15:28:10</p>
    	</div>
    </div>
    <!--操作-->
    <div class="operation-box">
    	<div class="operation-btn" @click="view_logistics">查看物流</div>
    	<div class="operation-btn" @click="chat_seller">私信卖家</div>
    </div>
  </div>
</template>

<script>
	export default({
		name : 'orderDetail' ,
		data(){
			return{
				
			}
		},
		created(){
//			console.log(this.$store.state.num)
		},
		methods:{
			//查看物流
			view_logistics(){
				
			},
			//私信卖家
			chat_seller(){
				
			}
		}
	})
</script>

<style lang="less" scoped>
	.orderDetail{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: #f5f5f5;
		padding-bottom: 1rem;
		overflow-y: scroll;
		.orderStatue{
			background: #FC8074;
			padding: .4rem;
			color: #fff;
			font-size: .36rem;
			letter-spacing: 0.02rem;
			margin-bottom: .2rem;
		}
		/*头部地址*/
		.mod-address{
			margin-bottom: .2rem;
			.mod-cell{
				height: auto;
				min-height: 1.6rem;
				padding: .2rem .3rem;
				font-size: .28rem;
				background:#fff;
				.mod-bd{
					padding: 0 .3rem;
					h4{
						font-size:.32rem ;
						color: #202020;
					}
					p{
						font-size: .24rem;
						color: #404040;
					}
				}
			}
		}
		/*订单详情部分*/
		.order_detailInfo{
			background: #fff;
			.title-box{
				padding: 0 .3rem;
				p{
					flex: 1;
					font-size: .28rem;
					line-height: .77rem;
					color: #404040;
				}
			}
			.order-info{
				background: #F9F9F9;
				padding: .2rem .3rem;
				.img-box{
					height: 1.5rem;
					width: 1.5rem;
					img{
						height: 100%;
						width: 100%;
					}
				}
				.info{
					position: relative;
					padding: 0 0 0 .2rem;
					height: 1.5rem;
					flex: 1;
					.title{
						font-size: .24rem;
    				color: #202020;
				    width: 5.2rem;
				    line-height: .42rem;
				    overflow: hidden;
				    -o-text-overflow: ellipsis;
				    text-overflow: ellipsis;
				    white-space: normal;
				    display: -webkit-box;
				    -webkit-line-clamp: 2;
				    -webkit-box-orient: vertical;
					}
					.current{
						position: absolute;
						bottom: 0;
						width: calc(~'100% - 0.2rem');
						font-size: .24rem;
						.price{
							color: #E8575E;
						}
					}
				}
			}
			/*服务承诺样式*/
			.commitment-box{
				padding: .1rem .3rem;
				color: #999;
				font-size: .24rem;
				border-bottom: 0.02rem solid #f0f0f0;
				div{
					i{
						color: #E8575E;
						margin: 0 .1rem;
					}
				}
			}
			/*众筹价格*/
			.fee-box{
		    padding: .3rem;
		    background-color: #fff;
		    font-size: .26rem;
		    color: #999;
		    border-bottom: .005rem solid #f0f0f0;
				.fee-item{
					padding:0 .1rem;
					p:nth-child(1){
						flex:1;
						margin-bottom: .1rem;
					}
				}
			}
			/*总价样式*/
			.total-fee{
			  padding: 0 .35rem;
				font-size: .3rem;
				margin-bottom: 0.2rem;
				.key-p{
					flex: 1;
					line-height: .84rem;
					color: #404040;
				}
				.value-p{
					color: #E8575E;
				}
			}
		}
		/*订单编号部分样式*/
		.order_number{
			padding: 0.2rem 0.3rem;
			background: #fff;
			font-size: .28rem;
			.order_number_item{
				p:nth-child(1){
					margin-right: 0.1rem;
				}
				p{
					line-height: .6rem;
				}
			}
		}
		/*操作*/
		.operation-box{
			position: fixed;
	    bottom: 0;
	    width: calc(~'100% - 0.6rem');
	    display: flex;
	    flex-direction: row-reverse;
	    padding: .25rem .3rem .25rem .3rem;
	    background-color: #fff;
	    box-shadow: 0 -3px 10px #e2e2e2;
	    div:nth-child(1){
	    	margin-right: 0;
	    } 
	    .operation-btn{
	    	font-size: .28rem;
		    margin-right: .3rem;
		    padding: 0 .2rem;
		    line-height: .5rem;
		    color: #404040;
		    border: .01rem solid #404040;
		    border-radius: .1rem;
	    }
		}
	}
</style>


